<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>
        <a href="/">商品管理</a>
      </el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddDialog">添加分类</el-button>
        </el-col>
      </el-row>

      <!-- 商品分类表格区域 -->
      <tree-table
        :data="cateList"
        :columns="columns"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        :selection-type="false"
        :expand-type="false"
      >
        <!-- 是否有效 -->
        <template #isOk="{row}">
          <i class="el-icon-success" style="color:lightGreen" v-if="row.cat_deleted===false"></i>
          <i class="el-icon-error" style="color:red" v-else></i>
        </template>
        <!-- 排序 -->
        <template #order="{row}">
          <el-tag size="small" v-if="row.cat_level===0">一级</el-tag>
          <el-tag type="success" size="small" v-else-if="row.cat_level===1">二级</el-tag>
          <el-tag type="warning" size="small" v-else-if="row.cat_level===2">三级</el-tag>
        </template>
        <!-- 操作 -->
        <template slot="operation">
          <el-button type="primary" size="small" icon="el-icon-edit">编辑</el-button>
          <el-button type="danger" size="small" icon="el-icon-delete">删除</el-button>
        </template>
      </tree-table>
      <!-- 页脚区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog
      title="添加分类"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addCateDialogClose"
    >
      <el-form
        :model="addClassifyForm"
        :rules="addClassifyRules"
        ref="addClassifyRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addClassifyForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            v-model="selectedKeys"
            :options="parentCate"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
            ref="cascaderRef"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 商品分类数据列表
      cateList: [],
      // 商品总数
      total: 0,
      // 商品分类请求参数
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      columns: [{
        label: '分类名称',
        prop: 'cat_name'
      }, {
        label: '是否有效',
        type: 'template',
        template: 'isOk'
      }, {
        label: '排序',
        type: 'template',
        template: 'order'
      }, {
        label: '操作',
        type: 'template',
        template: 'operation'
      }],
      addDialogVisible: false,
      addClassifyForm: {
        cat_name: '',
        // 分类父ID
        cat_pid: 0,
        // 分类级别
        cat_level: 0
      },
      addClassifyRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类列表
      parentCate: [],
      // 指定级联选择器的配置对象
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true

      },
      // 选中的父级ID数组
      selectedKeys: []
    }
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$http.get('categories', { params: this.querInfo })
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('获取商品列表失败')
      this.cateList = res.data.result
      // console.log(this.cateList)
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.querInfo.pagesize = newSize
      this.getCateList()
    },
    handleCurrentChange (newNum) {
      this.querInfo.pagenum = newNum
      this.getCateList()
    },
    showAddDialog () {
      this.addDialogVisible = true
      this.getParentCate()
    },
    async getParentCate () {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) return this.$message.error('获取父级分类失败')
      // console.log(res)
      this.parentCate = res.data
      // console.log(this.parentCate)
    },
    parentCateChanged () {
      // console.log(this.selectedKeys)
      if (this.selectedKeys.length === 0) {
        this.addClassifyForm.cat_pid = 0
        this.addClassifyForm.cat_level = 0
      } else {
        this.addClassifyForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        this.addClassifyForm.cat_level = this.selectedKeys.length
      }
    },
    addCate () {
      // console.log(this.addClassifyForm)
      this.$refs.addClassifyRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addClassifyForm)
        if (res.meta.status !== 201) return this.$message.error('添加商品分类失败')
        this.$message.success('添加商品分类成功')
        this.getCateList()
        this.addDialogVisible = false
      })
    },
    // 关闭添加分类模态框清空表单数据
    addCateDialogClose () {
      this.$refs.addClassifyRef.resetFields()
      this.selectedKeys = []
      this.addClassifyForm.cat_pid = 0
      this.addClassifyForm.cat_level = 0
      // console.log(this.addClassifyForm)
    }
  },
  created () {
    this.getCateList()
  },
  watch: {
    selectedKeys () {
      if (this.$refs.cascaderRef) {
        // console.log(this.$refs.cascaderRef)
        this.$refs.cascaderRef.dropDownVisible = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.el-row {
  margin-bottom: 15px;
}
.el-scrollbar__wrap {
  height: 300px;
}
</style>
